import React, { Component } from 'react';
import './App.css';
import Movie from './Movie';


class App extends Component {
  //// Component Lifecycle  /////

  // Render : componentWillMount() -> render() -> componentDidMount()
  // update : componentWillReceiveProps() -> shouldComponentUpdate() == true -> componentWillUpdate() -> render() -> componentDidUpdate()
  //(1)WillMount , (2)render , (3)DidMount
  // 컴포넌트 안에 state가 바뀔 때 마다, render가 발생!
  state = {}

  componentWillMount(){
    console.log('(1) will mount');
  }
   //이 싸이클은 자동으로 발생 내가 원하든 원하지않든!
  componentDidMount(){
    console.log('(3) did mount');
    this._getMovies();
  }

  _renderMovies = () => {
      const movies = this.state.movies.map( movie => {
        return <Movie 
          title={movie.title_english} 
          poster={movie.large_cover_image} 
          genres={movie.genres} 
          synopsis = {movie.synopsis}
          key={movie.id} />
      })
      return movies
  }

  // async를 안쓰면 await 작동 x
  _getMovies =  async  () => {
    const movies = await this._callApi()
    this.setState({
      movies
    })
  }
  _callApi = () => {
    return fetch('https://yts.lt/api/v2/list_movies.json?sort_by=download_count')
    .then(response => response.json())
    .then(json => json.data.movies)
    .catch(err => console.log(err))
  }

  render(){
    console.log('(2) rendering now');
    const {movies} = this.state;
    return (
      <div className={movies ? "App" : "App--loading"}>
        {movies ? this._renderMovies() : 'Loading'}
      </div>
    );
  }
}

export default App;
